import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state={count:3};
    }

    operateClick() {
        this.setState({count:this.state.count-1});
    }

    startClick() {
        this.setState({count:3});
    }

    render() {
        const audio=new Audio("/screem.mp3")
        if (this.state.count===0) {
            audio.play();
        }
        if (this.state.count<0) {
            this.setState({count:0});
        }

        return(
            <div className="container">
                <h1 className="h1 title">しり○うからの<br/>リクエストサイト</h1>
                <p className="h1">あと{this.state.count}回</p>
                <button className="btn btn-primary btn-lg btn-block" onClick={()=>{this.operateClick()}}>数を減らす</button>
                <button className="btn btn-info btn-lg btn-block" onClick={()=>{this.startClick()}}>最初から</button>
            </div>
        );
    }
}

  // ========================================
  
  ReactDOM.render(
    <App />,
    document.getElementById('root')
  );
  
